html {
  font-size: 10px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #333333;
  padding-top: 4rem;
}

.main-title {
  font-size: 7rem;
  margin: 0 auto;
  color: #fff;
}

nav .navbar-brand {
  font-size: 2.6rem;
}

.add-movie-btn {
  font-size: 2rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-list {
  margin-top: 5rem;
}

.box {
  min-height: 70rem;
  margin-bottom: 3rem;
  font-size: 1.5rem;
  background: #fff;
  display: flex;
  flex-flow: column;
  padding-bottom: 4rem;
  border-radius: 0.5rem;
  box-shadow: 1px 2px 4px 1px rgba(0, 0, 0, 0.2);
}

.box-img-top {
  min-height: 40rem;
  max-height: 40rem;
}

.box-body {
  margin: auto 0;
  padding: 0 1.5rem;
  display: flex;
  flex-flow: column;
}

.box-title {
  font-size: 2.5rem;
  font-weight: bold;
}

.box-buttons {
  margin: auto auto 0;
}

.box-button {
  margin: auto 0.3rem;
  max-width: 100%;
  width: 10rem;
  font-size: 1.5rem;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}

.modal-main {
  position: fixed;
  background: white;
  font-size: 1.6rem;
  width: 94%;
  max-width: 40rem;
  min-height: 20rem;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-evenly;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  padding: 2rem;
  text-align: center;
  border-radius: 5px;
}

.modal-title {
  font-size: 2.2rem;
}

.modal-close {
  background: transparent;
  color: #333;
  font-size: 2.2rem;
  border: none;
  position: absolute;
  top: 0;
  right: 0.3rem;
  cursor: pointer;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.add-modal {
  margin: 0;
  justify-content: center;
}

.add-modal-title {
  margin-top: 0;
}

.modal-main .form-control {
  padding: 1.4rem 1rem;
  width: 20rem;
  font-size: 1.4rem;
}

.modal-main label {
  width: 100%;
  max-width: 3rem;
  margin-right: 4rem;
}

.edit-form-group {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  flex: 0 0 auto;
}

.load-modal {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #ffffff;
  justify-content: center;
  align-items: center;
  top: 0;
}

